.card {
  display: flex;
  flex-direction: column;

  width: $card-width;
  padding: 15px 14px;
  
  font-size: $font-size-s;
  
  border: 1px solid $card-borders-color;
  border-radius: 5px;

  &__image {
    box-sizing: content-box;
    
    width: 160px;
    height: 134px;
    
    margin: 0 auto 30px;
    padding: 12px 0;

    border: solid 3px transparent;
    
    transition: transform 1s;

    &:hover {
      border-color: $card-borders-color;
      transform: scale(1.2);
    }
  }

  &__heading {
    margin-bottom: 5px;

    line-height: 15px;
    font-size: 12px;
    font-weight: 500;
  }

  &__product-id {
    display: block;

    margin-bottom: 15px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;

  }

  &__stars {
    margin-bottom: 20px;
  }
  
  &__feedbacks {
    line-height: 16px;
  }

  &__price-box {
    display: flex;
    align-items: flex-end;

    margin-bottom: 20px;
  }
  
  &__price {
    margin-left: auto;

    font-size: 16px;
    font-weight: bold;
  }

  &__link {
    display: block;
    
    font-size: 14px;
    line-height: 38px;
    text-align: center;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    
    background-color: #00acdc;
    border: 1px solid transparent;
    border-radius: 5px;

    transition: color 0.5s, border 0.5s, background-color 0.5s;

    &:hover {
      color: #00acdc;

      background-color: #fff;
      border: 1px solid #00acdc;
    }

  }

}
